import React, { Suspense, lazy, useEffect, useState,useCallback,memo } from "react";
import axios from "axios";
import { fetchAllPostsAction } from "../../redux/slices/Posts";
import { useSelector, useDispatch } from "react-redux";
import { isEmpty, filterpost } from "../Utils";
import { SERVER_URL } from "../../Domain";
import CardSkeleton from "../Skeleton/CardSkeleton";
import Button from '@mui/material/Button';
import Snackbar from '@mui/material/Snackbar';
import IconButton from '@mui/material/IconButton';
import MuiAlert from '@mui/material/Alert';
// import Card from "./Card";
const fakeDalay = async (promise) => {
  await new Promise((resolve) => {
    setTimeout(resolve, 3000);
  });
  return promise;
}
const Card = lazy(() => fakeDalay(import("./Card")))

const Alert = React.forwardRef(function Alert(props, ref) {
  return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});
const Posts = ({ socket, type,paramsId, hisprofile,postId}) => {
  const dispatch = useDispatch();
  const [loadPost, setLoadpost] = useState(true);
  const [count, setCount] = useState(5);
  const [result,setResutl] = useState()
  const posts = useSelector((state) => state.posts);
  const allpost = useSelector((state) => state.Allpost);
  const userData = useSelector((state) => state.userInfo)
  const [failLoded,setfailLoded] = useState(false)
  const [open, setOpen] = useState(false);
  const loadMore = () => {
    if (
      window.innerHeight + document.documentElement.scrollTop + 1 >
      document.scrollingElement.scrollHeight - 50
    ) {
      setLoadpost(true);
    }
  };
  //============================================================================/
  const handleClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }

    setOpen(false);
  };
  const action = (
    <>
      <Button color="secondary" size="small" onClick={handleClose}>
        UNDO
      </Button>
      <IconButton
        size="small"
        aria-label="close"
        color="inherit"
        onClick={handleClose}
      >
        
      </IconButton>
    </>
  );
  //==============================================================================
  useEffect(() => {
    if (loadPost) {
      const fetchData = async () => {
        await axios({
          method: "GET",
          url: SERVER_URL + "/api/post/",
          withCredentials: true,
        })
          .then(({ data }) => {
            // console.log("fetch again from posts")
            const Array = filterpost(data, userData.following, userData._id).slice(0, count);
            for (var i = 0; i < Array.length; i++) {
              Array[i].comments.reverse()
            }
            dispatch(fetchAllPostsAction.fetchAllPosts(Array));
            setLoadpost(false);
            setCount(c => c + 5);
          })
          .catch((err) => {
          setfailLoded(err.message)
          setLoadpost(false)
          setOpen(true)
          });
      };
      fetchData();
    }
  }, [loadPost]);
  useEffect(() => {
    window.addEventListener("scroll", loadMore);
    return () => window.removeEventListener("scroll", loadMore);
  }, [])
  const fov = useCallback(()=>{
     const bb = allpost.filter((post)=>post._id === postId)
     setResutl(bb)
  },[allpost])
  useEffect(()=>{
  fov()
  console.log(result)
  },[fov])
  const refreshpost = useCallback(()=>{
    setLoadpost(true)
    setfailLoded(false)
  },[])
  return (
    <div className={paramsId ? "hisPosts":"feeds"}>
      {type === "home" && !isEmpty(posts[0]) &&
        posts.map((post,index) => {
          return <Suspense key={index} fallback={<CardSkeleton key={Math.random()} />}>
            <Card
              post={post} key={post._id}
              count={count}
              socket={socket}
            /></Suspense>;
        })}
      {type === "profile" && !isEmpty(posts[0]) &&
        posts.map((post,index) => {
          return <Suspense key={index} fallback={<CardSkeleton key={Math.random()} />}>
            {
              post.posterId === userData._id &&
              <Card
                post={post} key={post._id}
                count={count}
                socket={socket}
                key={Math.random()}
              />
            }
          </Suspense>;
        })}
        {type === "hisprofile" && paramsId && !isEmpty(posts[0]) &&
        posts.map((post,index) => {
          return <Suspense key={index} fallback={<CardSkeleton key={Math.random()} />}>
          {
            post.posterId === paramsId &&
              <Card
                post={post} key={post._id}
                count={count}
                socket={socket}
                key={Math.random()}
              />            
          }

          </Suspense>;
        })}
          {type === "single"  && !isEmpty(allpost[0]) &&postId&&
        allpost.map((post,index) => {
          return <Suspense key={index} fallback={<CardSkeleton key={Math.random()} />}>
          {
            post._id === postId &&
              <Card
                post={post} 
                count={1}
                socket={socket}
                single={type}
                key={Math.random()}
              />            
          }

          </Suspense>;
        })}
        {
        failLoded &&  <div style={{cursor:"pointer"}} onClick={refreshpost} className="cardCanvas">{failLoded}</div>
        }
        {
        loadPost && <CardSkeleton/>
        }
         <Snackbar
        open={open}
        autoHideDuration={6000}
        onClose={handleClose}
        action={action}
      >
      <Alert onClose={handleClose} severity="error" sx={{ width: '100%' }}>
       CONNECTION WAS LOST
        </Alert>
      </Snackbar>
    </div>
  );
};

export default memo(Posts);
